class Api {
  constructor({ baseUrl }) {
    this._baseUrl = baseUrl;
  }

  // 
  _checkRes(res) {
    if (res.ok) {
      return res.json()
    }
    return Promise.reject(`: ${res.status}`)
  }

  //   
  getCardInfo() {
    return fetch(`${this._baseUrl}/cards`, {
      credentials: 'include',
    })
      .then(res => this._checkRes(res))
  }

  // 
  getUserData() {
    return fetch(`${this._baseUrl}/users/me`, {
      credentials: 'include',
    })
      .then(res => this._checkRes(res))
  }

  //   
  updateUserInfo({ name, about }) {
    return fetch(`${this._baseUrl}/users/me`, {
      method: 'PATCH',
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        name: name,
        about: about
      })
    })
      .then(res => this._checkRes(res))
  }

  //  
  updateUserAvatar({ avatar }) {
    return fetch(`${this._baseUrl}/users/me/avatar`, {
      method: 'PATCH',
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        avatar: avatar
      })
    })
      .then(res => this._checkRes(res))
  }

  // 
  _addLike(cardId) {
    return fetch(`${this._baseUrl}/cards/${cardId}/likes`, {
      method: 'PUT',
      credentials: 'include',
    })
      .then(res => this._checkRes(res))
  }

  // 
  _deleteLike(cardId) {
    return fetch(`${this._baseUrl}/cards/${cardId}/likes`, {
      method: 'DELETE',
      credentials: 'include',
    })
      .then(res => this._checkRes(res))
  }

  changeLikeCardStatus(cardId, isLiked) {
    if (isLiked) {
      return this._deleteLike(cardId)
    } else {
      return this._addLike(cardId)
    }
  }

  addCard({ name, link }) {
    return fetch(`${this._baseUrl}/cards`, {
      method: 'POST',
      credentials: 'include',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        name: name,
        link: link
      })
    })
      .then(res => this._checkRes(res))
  }

  deleteCard(cardId) {
    return fetch(`${this._baseUrl}/cards/${cardId}`, {
      method: 'DELETE',
      credentials: 'include',
    })
      .then(res => this._checkRes(res))
  }
}

const api = new Api({
  baseUrl: 'http://localhost:3000'
});

export default api;
